.container {
    padding: 40px 0;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    flex-grow: 1;
}

.blockTitle {
    width: 100%;
    height: 40px;
    background-color: #69c0ff;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    color: #f8f9fa;
}

.token {
    background-color: white;
    box-shadow: 5px 5px 5px 0 #DBDBDB;
    border: solid 0 darkgrey;
    border-radius: 5px;
    overflow: hidden;
    margin-bottom: 20px;
}

.profile{
    display: flex;
    justify-content: space-between;
    flex: 1 1 auto;
    margin: 20px 20px 0 20px;
}

.tokenContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 20px;
}

.block {
    max-width: 800px;
    flex-grow: 1;
    margin: 10px;
}

.info {
    display: flex;
    flex-direction: column;
    height: 200px;
    align-items: center;
    background-color: white;
    box-shadow: 5px 5px 5px 0 #DBDBDB;
    border: solid 0 darkgrey;
    border-radius: 5px;
    overflow: hidden;
}

.table {
    background-color: white;
    box-shadow: 5px 5px 5px 0 #DBDBDB;
    border-radius: 5px;
    overflow: hidden;
    margin-bottom: 20px;
    align-self: flex-start;
}

.infoContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px 40px 20px 20px;
}

.button {
    margin-top: 10px;
}

.block2 {
    margin: 10px;
    max-width: 800px;
    flex-grow: 1;
}

.tableTitle {
    height: 40px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #f8f9fa;
    font-size: 18px;
    background-color: #69c0ff;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.userDataForm{
    flex-grow: 1;
    flex-direction: column;
    order: 1;
}

.photo {
    align-self: flex-start;
    position: relative;
    text-align: center;
    margin-left: 20px;
    margin-bottom: 20px;
    color: white;
    order: 2;
}

input[type="file"] {
    display: none;
}

.customFileUpload {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
    font-size: 20px;
}

.mainPhoto {
    max-width: 140px;
    border-radius: 100%;
    border: 1px solid gainsboro;
}

@media (max-width: 992px) {
    .profile {
        flex-direction: column;
    }
    .userDataForm {
        order: 2;
    }
    .photo {
        order: 1;
        align-self: center;
    }
}

@media (max-width: 1200px) {
    .container {
        align-items: center;
    }
    .block2 {
        align-self: center;
        width: 100%;
        max-width: 700px;
    }
    .block {
        width: 100%;
        max-width: 700px;
    }
}